.image-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 0 10px;
    grid-auto-rows: 10px;
}

.image-list img {
    width: 250px;
}
.steamImg {
    background: url(https://steamcdn-a.akamaihd.net/steamcommunity/public/images/steamworks_docs/english/sits_large_border.png);
    padding: 25px 58px;
}

.btn:hover {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
}
